
.mobile_footer {
    padding: 0;
    .cell {
        padding-left: 2em;
        padding-right: 2em;
    }
    .footer_logo {
        max-width: 600px;
        margin: auto;
        display: block;
        padding: 1em;
    }
    .contact-information {
        max-width: 600px;
        margin: auto;
        text-align: center;
    }
    .accordion {
        margin: auto;
        @include box-shadow(2);
    }
}

.footerLogo {
    width: 150px;
    display: inline-block;
}

.footer-bottom {
    font-size: 12px;
    display: flex;
    background: #fff;
    color: #333;
    padding: 20px 0;
    border-top: 1px solid #ececec;
    ul {
        display: flex;
        li {
            display: inline-flex;
        }

    }
}


















.nasa-footer-info-box {
    margin-top: 15px;
